import { Component, OnInit,Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { StaffService } from 'src/app/api/controllers/Staff';
import { PackageService } from 'src/app/api/controllers/Package';

import { LookupService } from 'src/app/api/controllers/Lookup';
//import { SharedService } from 'src/app/api/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-create-package',
  templateUrl: './create-package.component.html',
  styleUrls: ['./create-package.component.scss']
})
export class CreatePackageComponent implements OnInit {

  isEdit: boolean = false;
  isError: boolean = false;
  isPackageCreated:  boolean = true;
  packageData: any;
  staffListData: any;
  packageTypes: any;
  deliveryTypes: any;
  deliveryStatus: any;
  deliverySlots: any;
  receiverType: any;

  public params = {
		apartmentId: parseInt(this.cookieService.get('apartmentId'))
	  }

  constructor(  private router: Router,
    private route: ActivatedRoute,
    private staffService:StaffService,
    private packageService:PackageService,
    private lookupService: LookupService,
    private apartmentService: ApartmentService,
    //private sharedService:SharedService,
    private cookieService:CookieService) { }


    submitPackageForm(form){

        let package_data = {

          "apartmentId": parseInt(this.cookieService.get('apartmentId')),
          "apartmentBlockUnitId":this.packageData.BlockUnitId,
          "packageTypeId": this.packageData.Type,
          "slot": this.packageData.Slot,
          "deliveryTypeId": this.packageData.Deliverytype,
          "deliveryDate": new Date().toISOString(),
          "assignedTo": this.packageData.Staff,
          "description": this.packageData.Comments,
          "receivedBy": parseInt(this.cookieService.get('userId')),
          "deliveryStatusId": 986,
          "isActive": true,
          "insertedBy": parseInt(this.cookieService.get('userId')),
          "insertedOn": new Date().toISOString(),
          "updatedBy": 0,
          "updatedOn": null
        }
        let params:any = {
          utilityTrackerElectricityPeriod: package_data
        }

        this.packageService.addPackage(params ).subscribe((package_res:any) =>{

        })
    }

  ngOnInit(): void {
    this.packageData = {};
    this.packageData.Deliverytype = "";
    this.packageData.Staff = "";
    this.packageData.Slot = ""
    this.packageData.Slot = "";

    //staff type
    this.staffService.getAllStaffsByApartmentId(this.params).subscribe((res:any) => {
    this.staffListData = res.filter(data => {
      return data.isActive
    });
    });


    let packgeTypeParams = {
      LookupTypeId: 169
    }
    this.lookupService.getLookupValueByLookupTypeId(packgeTypeParams).subscribe((res:any) => {

       this.packageTypes = res.filter( item => {
          return item.isActive;
       });
    },
    error => {
    });


    let deliveryTypeParams = {
      LookupTypeId: 170
    }
    this.lookupService.getLookupValueByLookupTypeId(packgeTypeParams).subscribe((res:any) => {

       this.deliveryTypes = res.filter( item => {
          return item.isActive;
       });
    },
    error => {
    });


    let deliveryStatusParams = {
      LookupTypeId: 171
    }
    this.lookupService.getLookupValueByLookupTypeId(packgeTypeParams).subscribe((res:any) => {

       this.deliveryStatus = res.filter( item => {
          return item.isActive;
       });
    },
    error => {
    });


    let deliverySlotParams = {
      LookupTypeId: 171
    }
    this.lookupService.getLookupValueByLookupTypeId(packgeTypeParams).subscribe((res:any) => {

       this.deliverySlots = res.filter( item => {
          return item.isActive;
       });
    },
    error => {
    });

    let receiverTypeParams = {
      LookupTypeId: 182
    }
    this.lookupService.getLookupValueByLookupTypeId(receiverTypeParams).subscribe((res:any) => {

       this.receiverType = res.filter( item => {
          return item.isActive;
       });
    },
    error => {
    });

  }

}
